
'use client';

import type { ReactNode } from 'react';
import { createContext, useContext, useState, useEffect, useCallback } from 'react';
import { DEFAULT_LOCALE, type Language, SUPPORTED_LANGUAGES } from '@/config/languages';

interface LocaleContextType {
  locale: Language['code'];
  setLocale: (locale: Language['code']) => void;
  supportedLanguages: Language[];
}

const LocaleContext = createContext<LocaleContextType | undefined>(undefined);

export function LocaleProvider({ children }: { children: ReactNode }) {
  const [locale, _setLocale] = useState<Language['code']>(DEFAULT_LOCALE);

  useEffect(() => {
    const storedLocale = localStorage.getItem('nextgen-ecourt-locale') as Language['code'] | null;
    if (storedLocale && SUPPORTED_LANGUAGES.some(lang => lang.code === storedLocale)) {
      _setLocale(storedLocale);
    } else {
      // Optionally, could try to detect browser language here
      _setLocale(DEFAULT_LOCALE);
    }
  }, []);

  const setLocale = useCallback((newLocale: Language['code']) => {
    if (SUPPORTED_LANGUAGES.some(lang => lang.code === newLocale)) {
      _setLocale(newLocale);
      localStorage.setItem('nextgen-ecourt-locale', newLocale);
      document.documentElement.lang = newLocale; // Update lang attribute on html tag
    } else {
      console.warn(`Attempted to set unsupported locale: ${newLocale}`);
    }
  }, []);
  
  useEffect(() => {
    document.documentElement.lang = locale;
  }, [locale]);

  return (
    <LocaleContext.Provider value={{ locale, setLocale, supportedLanguages: SUPPORTED_LANGUAGES }}>
      {children}
    </LocaleContext.Provider>
  );
}

export function useLocale() {
  const context = useContext(LocaleContext);
  if (context === undefined) {
    throw new Error('useLocale must be used within a LocaleProvider');
  }
  return context;
}
