
'use client';

import { useState, useEffect, useCallback } from 'react';
import { useLocale } from '@/contexts/LocaleContext';
import { DEFAULT_LOCALE, type Language } from '@/config/languages';

type Translations = Record<string, string>;

export function useTranslation() {
  const { locale } = useLocale();
  const [translations, setTranslations] = useState<Translations>({});
  const [isLoading, setIsLoading] = useState(true);
  const [currentLocaleUsed, setCurrentLocaleUsed] = useState(locale);

  useEffect(() => {
    setIsLoading(true);
    async function loadTranslations(loc: Language['code']) {
      try {
        const module = await import(`@/locales/${loc}.json`);
        setTranslations(module.default || module);
        setCurrentLocaleUsed(loc);
      } catch (error) {
        console.error(`Failed to load translations for locale: ${loc}`, error);
        if (loc !== DEFAULT_LOCALE) {
          try {
            console.warn(`Falling back to ${DEFAULT_LOCALE} locale.`);
            const fallbackModule = await import(`@/locales/${DEFAULT_LOCALE}.json`);
            setTranslations(fallbackModule.default || fallbackModule);
            setCurrentLocaleUsed(DEFAULT_LOCALE);
          } catch (fallbackError) {
            console.error(`Failed to load fallback ${DEFAULT_LOCALE} translations`, fallbackError);
            setTranslations({}); // Empty translations if everything fails
          }
        } else {
          setTranslations({});
        }
      } finally {
        setIsLoading(false);
      }
    }
    loadTranslations(locale);
  }, [locale]);

  const t = useCallback((key: string, params?: Record<string, string | number | undefined>): string => {
    if (isLoading) return key; // Or a loading indicator string

    let translation = translations[key];

    if (translation === undefined) {
      console.warn(`Translation key "${key}" not found for locale "${currentLocaleUsed}".`);
      return key; // Return key if not found
    }
    
    if (params) {
      Object.keys(params).forEach(paramKey => {
        const value = params[paramKey];
        if (value !== undefined) {
           translation = translation.replace(new RegExp(`{${paramKey}}`, 'g'), String(value));
        }
      });
    }
    return translation;
  }, [translations, isLoading, currentLocaleUsed]);

  return { t, isLoadingTranslations: isLoading, currentLocale: locale };
}
